//Последний блок в каталоге - показать больше
.products-more {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 78px;
  padding: 25px 0 25px 0;

  &::after {
    position: absolute;
    width: 100vw;
    height: 1px;
    bottom: 0;
    left: calc(50% - 50vw);
    background-color: $bg-color-catalog;
    content: "";
  }
}

.products-more__title {
  margin-bottom: 8px;
  padding-top: 53px;

  @include font_heading($font-regular);
  @include font_size_text(16px, 20px);
  text-transform: uppercase;
  text-align: center;
  color: $font-color-additional;

  &::before {
    position: absolute;
    width: 30px;
    height: 30px;
    top: 25px;
    left: 50%;
    background-color: transparent;
    background-image: url("../img/catalog/plus.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    transform: translateX(-50%);
    content: "";
  }

  @media (min-width: $min-tablet) {
    max-width: 134px;
    margin-bottom: 23px;

    @include font_size_text(20px, 22px);
  }
}

.products-more__text {
  margin-bottom: 21px;

  @include font_size_text(12px, 16px);
  color: $font-color-footer;
  text-align: center;

  @media (min-width: $min-tablet) {
    max-width: 167px;
    margin-bottom: 87px;

    @include font_size_text(16px, 18px);
  }
}

.products-more__show {
  width: 100%;
  padding: 10px 0;

  @include font_heading($font-regular);
  @include font_size_text(16px, 20px);
  color: $font-color-footer;

  @media (min-width: $min-tablet) {
    padding: 20px 0;
  }
}
